import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Input, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import esLocale from '@fullcalendar/core/locales/es';
import 'react-datepicker/dist/react-datepicker.css'
import ReactDatePicker from 'react-datepicker';

function getDateStr(str) {
  return new Date(str).toISOString().replace(/T.*$/, '');
}

function generateUUID() { // Public Domain/MIT
  var d = new Date().getTime();//Timestamp
  var d2 = ((typeof performance !== 'undefined') && performance.now && (performance.now()*1000)) || 0;//Time in microseconds since page-load or 0 if unsupported
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      var r = Math.random() * 16;//random number between 0 and 16
      if(d > 0){//Use timestamp until depleted
          r = (d + r)%16 | 0;
          d = Math.floor(d/16);
      } else {//Use microseconds since page-load if supported
          r = (d2 + r)%16 | 0;
          d2 = Math.floor(d2/16);
      }
      return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
  });
}

const ModalNew = (props) => {
  return (  <Modal {...props}>
    <ModalHeader toggle={props.toggle}>
      Agregar Evento
    </ModalHeader>
    <ModalBody>
      <Row>
        <Col sm="6" xs="12">Fecha:</Col>
        <Col sm="6" xs="12">
          <ReactDatePicker
            className='form-control'
            onChange={props.setDate}
            selected={props.date} 
            dateFormat="PP"
            isClearable = {false}
            clearButtonClassName="text-dark"
          />
        </Col>
      </Row>
      <Row className='mt-2'>
        <Col sm="6" xs="12">Descripcin:</Col>
        <Col sm="6" xs="12">
          <Input
            id="description"
            name="description"
            type="textarea"
            value={props.description}
            onChange={(e) => props.setDescription(e.target.value)}
          />
        </Col>
      </Row>
    </ModalBody>
    <ModalFooter>
      <Button
        color="primary"
        disabled={!props.description}
        onClick={()=>{ props.addEvent(props.description, props.date); props.toggle(); }}
      >
        Agregar
      </Button>
      {' '}
      <Button onClick={props.toggle}>
        Cancelar
      </Button>
    </ModalFooter>
  </Modal>)
}


function App() {
  const [modal, setModal] = useState(false);
  const [date, setDate] = useState(new Date());
  const [description, setDescription] = useState("");
  const [events, setEvents] = useState([]);

  const handleDateSelect = (selectInfo) => {
    setDescription("");
    setDate(selectInfo.start);
    setModal(true);
  }

  const addEvent = (eventTitle, dateStart) => {
    const newEvents = [{
      id: 5,
      title: eventTitle,
      start: getDateStr(dateStart),
    }, ...events];
    setEvents(newEvents);
    localStorage.setItem('events', JSON.stringify(newEvents));
  }


  useEffect(() => {
    const theItems = localStorage.getItem('events');
    if (theItems != null ) {
      const items = JSON.parse(theItems);
      if (items) {
      setEvents(items);
      }
    }else{
      setEvents([{
        id: generateUUID(),
        title: 'T.P.1',
        start: getDateStr('2022-04-29T23:59:59'),
      },
      {
        id: generateUUID(),
        title: 'T.P.2',
        start: getDateStr('2022-05-20T23:59:59'),
      },
      {
        id: generateUUID(),
        title: 'T.P.3',
        start: getDateStr('2022-06-22T23:59:59'),
      },
      {
        id: generateUUID(),
        title: 'T.P. 4',
        start: getDateStr('2022-07-15T23:59:59'),
      },
      {
        id: 1,
        title: 'Cursado',
        start: getDateStr('2022-03-12T00:00:00'),
        end: getDateStr('2022-07-23T00:00:00'),
        display: 'background'
      }]);
    }
  }, []);

  return ( <>
    <header className="py-3 mb-4 border-bottom">
    <div className="container d-flex flex-wrap justify-content-center">
      <a href="/" className="d-flex align-items-center mb-3 mb-lg-0 me-lg-auto text-dark text-decoration-none">
        <span className="fs-4">Calendario Sem. Actualizacin en Sistemas Colaborativos </span>
      </a>
    </div>
  </header>
  <ModalNew 
        isOpen={modal} 
        toggle={() => setModal(false)} 
        date={date} 
        setDate={setDate} 
        description={description} 
        setDescription={setDescription}
        addEvent={addEvent}
        />
    <Container>
      <Row>
        <Col xl="10">
          <FullCalendar
            plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
            headerToolbar={{
              left: 'prev,next',
              center: 'title',
              right: 'dayGridMonth,timeGridWeek'
            }}
            themeSystem='bootstrap5'
            locale={esLocale}
            initialView='dayGridMonth'
            editable={false}
            selectable={true}
            selectMirror={false}
            dayMaxEvents={true}
            weekends={true}
            events={events}
            select={handleDateSelect}
          />
        </Col>
        <Col xl="2" className="mt-3">
            <Row>
              <Col xs="12" sm="6"><h5>Eventos:</h5></Col>
              <Col xs="12" sm="6"><Button  color="primary" onClick={() => setModal(true)}> Agregar </Button></Col>
              
            </Row>
            {events.map(event => {
              return(<Row key={event.id}>
                  <Col xs="12">
                    <strong>{event.title}</strong>
                  </Col>
                  <Col xs="12">
                    {event.start} {event.end ? `al ${event.end}` : ''}
                  </Col>
                </Row>);
            })}
        </Col>
      </Row>
    </Container>
    </> );
}

export default App;
